/***********************************
_media-queries.scss
Media Queries
***********************************/
@media screen and (min-width: 400px) {

  header .header__text-wrap {
    align-self: flex-end;
  }

  header .header-image {
    background-position: 40% -2em;
  }

  header .header__name {
    font-size: 2.2em;
  }
}



@media screen and (min-width: 600px) {

  html {
    font-size: 18px;
  }

  header {
    height: 98vh;
  }

  header .header__name {
    font-size: 3em;
    font-weight: 400;
  }

  header .header-image {
    background-position: 40% -2em;
  }

  header .header__content-wrap .header__text-wrap {
    justify-content: space-between;
  }


  .work .tertiary.work__client .client-content {
    flex-direction: row;

    .offset-screenshot {
      width: 60%;
      margin-bottom: 0;
      margin-top: 1em;
      box-shadow: none;
      align-self: center;

    }

  }

}

@media screen and (min-width: 900px) {

  html {
    font-size: 20px;
  }

  header .header-image {
    background-position: 40% 2em;
  }

  header .header__name {
    font-size: 4em;
  }

  .work .secondary.work__client.work__client--buyers {
    display: flex;
    flex-direction:row;
    align-items: center;
    max-height: 400px;
    // margin-bottom: 0;

    .work__client__img-wrapper {
      width: 50%;
      // align-self: center;
      padding-top: 0;
    }

    .client-details {
      // align-self: center;

    }
  }

  .work .work__header.header__h2, .work .work__header.header__flavor {
    // text-align: left;
    // padding-left: 5%;
  }

  .secondary.work__client--buyers {
     background: linear-gradient(180deg, $primary-bg, #eee 35%, #ccc 60%, $primary-bg 70%);
     background-image: linear-gradient(180deg, #ccc, #eee 30%, #fafafa 40%, $primary-bg 50%);

  }

  main .work .secondary.work__client > .work__client__img-wrapper {
      background-image: none;
  }




  #about #bio__text-wrap {
    padding-top: $section-spacing * 1.5;
    padding-bottom: $section-spacing * 1.5;

  }

  #work .client-details {  }

  .work__client--buyers {
    padding-top: $section-spacing;
    padding-bottom: $section-spacing;
  }

  main .work .tertiary-wrap {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    max-width: 1400px;
    // align-self: center;
    margin-left: auto;
    margin-right: auto;

    .work__client.tertiary {
      margin: 0;
      width: 30%;
      max-width: 395px;
      .client-content {
        flex: 1 1 auto;
      }
      .client-details__type {
        margin-left: 5%;
        margin-right: 5%;
      }
    }
  }


}

@media (min-width: 1200px) {

  .work__client--buyers {
    padding-top: $section-spacing;
    padding-bottom: $section-spacing;
  }

}

@media (min-width: 1400px) {
   header .header-image {
    background-position: -6em 0em;
  }

  .work .secondary.work__client.work__client--buyers .client-details {
    margin-left: 0;
  }

}


@media (min-width: 1800px) {}
